<template>
  <div id="app">
    <el-row>
      <el-col :span="24">
        
        <h1 class="theme"><img class="logo" src="./img/flash.png"></h1>
        <span></span>
      </el-col>
    </el-row>
    
    <el-row class="box">
      <el-col :span="3"><div class="title"></div></el-col>
      <el-col :span="21">
        <el-row>
          <el-col :span="4">
            <div class="choice">
              <span></span><br>
              <el-radio-group v-model="sex">
                <el-radio label="nan"></el-radio>
                <el-radio label="nv"></el-radio>
              </el-radio-group>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="choice">
              <span></span><br>
              <el-input class="short" :maxlength="6" size="small" v-model="age" @change="transAge" ></el-input> 
            </div>
          </el-col>
          <el-col :span="8" :offset="1">
            <div class="choice">
              <span></span><br>
              <el-cascader
                expand-trigger="hover"
                :options="indus"
                filterable
                size="small"
                v-model="job"
                :show-all-levels="false">
              </el-cascader>
            </div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>

    <el-row class="box">
      <el-col :span="16" class="border_r border_b">
        <el-row>
          <el-col :span="6" :offset="6">
            <div class="choice">
              <span></span><br>
              <el-radio-group v-model="year">
                <el-radio :label="10"></el-radio>
                <el-radio :label="20"></el-radio>
              </el-radio-group>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="choice">
              <span>CIF</span>
              <br>
              <el-input size="small" class="short" type="number" v-model.number="coverage" :min="1" :max="50" ></el-input> 
            </div>
          </el-col>
          
          <el-col :span="6">
            <div class="fee">
              <span>CIF </span>
              <br>
              <span :class="getColorClass(cifFee)">{{ cifFee | capitalize }}</span>
            </div>
          </el-col>
          
        </el-row>
      </el-col>
      <el-col :span="8" class="border_b">
        <el-row>
          <el-col :span="12">
            <div class="choice">
              <span>ADDC</span>
              <br>
              <el-input size="small" class="short" type="number" v-model.number="addcCoverage" :min="0" :max="99" ></el-input> 
            </div>
          </el-col>
          <el-col :span="12">
            <div class="fee">
              <span>ADDC </span>
              <br>
              <span :class="getColorClass(addcFee)">{{ addcFee | capitalize }}</span>
            </div>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="16" class="border_r">
        <el-row>
          <el-col :span="6">
            <div class="choice">
              <span></span>
              <br>
              <el-radio-group v-model="hasSocialSecurity">
                <el-radio :label="true"></el-radio>
                <el-radio :label="false"></el-radio>
              </el-radio-group>
            </div>
          </el-col>

          <el-col :span="6">
            <div class="choice">
              <span></span>
              <br>
              <el-radio-group v-model="isFirst">
                <el-radio :label="true"></el-radio>
                <el-radio :label="false"></el-radio>
              </el-radio-group>
            </div>
          </el-col>

          <el-col :span="6">
            <div class="choice">
              <span></span>
              <br>
              <el-select v-model="plan" class="middle" size="small" placeholder="">
                <el-option
                  v-for="item in planTop"
                  :label="chars[item - 1]"
                  :value="item - 1">
                </el-option>
              </el-select>
            </div>
          </el-col>
          <el-col :span="6" v-show="!hasSocialSecurity">
            <div class="fee">
              <span>HRC </span>
              <br>
              <span :class="getColorClass(hrcFee)">{{ hrcFee | capitalize }}</span>
            </div>
          </el-col>
          <el-col :span="6" v-show="hasSocialSecurity">
            <div class="fee">
              <span>HRD </span>
              <br>
              <span :class="getColorClass(hrdFee)">{{ hrdFee | capitalize }}</span>
            </div>
          </el-col>
        </el-row>
        
      </el-col>
      <el-col :span="8">
        <el-row>
          <el-col :span="12">
            <div class="choice">
              <span>AMRC</span>
              <br>

              <el-input type="number" size="small" class="short" :min="0" v-model.number="amrcCoverage"></el-input> 
            </div>
          </el-col>
          <el-col :span="12">
            <div class="fee">
              <span>AMRC </span>
              <br>
              <span :class="getColorClass(amrcFee)">{{ amrcFee | capitalize }}</span>
            </div>
          </el-col>
        </el-row>
        
      </el-col>
    </el-row>

    <el-row class="box">
      <el-col :span="3"><div class="title"></div></el-col>
      <el-col :span="21">
        <el-row>
          <el-col :span="4">
            <div class="choice">
              <span></span><br>
              <el-radio-group v-model="applicantSex">
                <el-radio label="nan"></el-radio>
                <el-radio label="nv"></el-radio>
              </el-radio-group>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="choice">
              <span></span><br>
              <el-input  class="short" :maxlength="6" size="small" v-model="applicantAge" @change="transApplicantAge"></el-input> 
            </div>
          </el-col>
          <el-col :span="7">
            <div class="choice">
              <span></span><br>
              <el-radio-group v-model="relation">
                <el-radio label="self"></el-radio>
                <el-radio label="parent"></el-radio>
                <el-radio label="other"></el-radio>
              </el-radio-group>
            </div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>

    <el-row class="box">
      <el-col :span="3"><div class="title">W</div></el-col>
      <el-col :span="21">
        <el-row>
          <el-col :span="8">
            <div class="choice">
              <span> </span>
              <br>
              <span>{{ totalFee | capitalize}}</span>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="fee">
              <span>WA </span>
              <br>
              <span :class="getColorClass(waFee)">{{ waFee | capitalize }}</span>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="fee">
              <span>WPA </span>
              <br>
              <span :class="getColorClass(wpaFee)">{{ wpaFee | capitalize }}</span>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="fee">
              <span>WPB </span>
              <br>
              <span :class="getColorClass(wpbFee)">{{ wpbFee | capitalize }}</span>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="fee">
              <span>WP </span>
              <br>
              <span :class="getColorClass(wpFee)">{{ wpFee | capitalize }}</span>
            </div>
          </el-col>
          
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import cifRates from './json/cif.json';
import hrcRates from './json/hrc.json';
import hrdRates from './json/hrd.json';
import addcRates from './json/addc.json';
import amrcRates from './json/amrc.json';
import waRates from './json/wa.json';
import wpaRates from './json/wpa.json';
import wpbRates from './json/wpb.json';
import wpRates from './json/wp.json';
import industries from './json/industryCategory.json';

export default {
  name: 'app',
  data () {
    return {
      sex: 'nan',
      year: 10,
      age: 20,
      hasSocialSecurity: false,
      isFirst: true,
      coverage: 10,
      applicantAge: 45,
      applicantSex: 'nan',
      indu: ' - ',
      chars: ['', '', '', '', '', '', ''],
      plan: 1,
      job: ["", "", "02060"],
      addcCoverage: 2,
      amrcCoverage: 5,
      relation: 'self',
      hasWA: true
    }
  },
  filters: {
    //
    capitalize: function(val) {
      
      //
      if ( (typeof val === 'string') && val.constructor === String) {
        return val;
      }

      //NaN
      if (isNaN(val) || val < 0) {
        return ''
      }

      //
      return val.toFixed(2) + ' ';
    },
  },
  computed: {
    //CIF
    cifFee: function () {
      return (cifRates[`${this.sex}_year${this.year}_age${this.age}`] + this.occupation.life) * this.coverage;
    },
    //hrc
    hrcFee: function () {
      //
      if (this.plan === 0 ) {
        return ''
      }

      //
      if (this.noFee(this.cifFee) ) {
        return ''
      }

      return Math.round(hrcRates[`occu${this.occupation.hospital}_group${this.ageGroup}_plan${this.plan}`] * (this.isFirst ? 0.95 : 1))
    },
    //hrd
    hrdFee: function () {
      //
      if (this.plan === 0 ) {
        return ''
      }

      //
      if (this.noFee(this.cifFee) ) {
        return ''
      }

      return Math.round(hrdRates[`occu${this.occupation.hospital}_group${this.ageGroup}_plan${this.plan}`] * (this.isFirst ? 0.95 : 1))
    },
    //addc
    addcFee: function () {
      
      //ADDC+<= 100
      if (this.age > 18 && this.coverage + this.addcCoverage > 100 ) {
        return '100'
      }

      //ADDC+<= 50
      if (this.age <= 18 && this.coverage + this.addcCoverage > 50 ) {
        return '50'
      }

      //
      if (this.noFee(this.cifFee) ) {
        return ''
      }

      return addcRates[`occu${this.occupation.accident}`] * 10 * this.addcCoverage
    },
    //amrc
    amrcFee: function () {
      
      //
      if (this.noFee(this.cifFee) ) {
        return ''
      }

      //ADDC
      if (this.noFee(this.addcFee) ) {
        return 'ADDC'
      }

      //AMRCADDC20%
      if (this.amrcCoverage > this.addcCoverage * 2 ) {
        return 'ADDC20%'
      }

      return amrcRates[`occu${this.occupation.accident}`] * 1 * this.amrcCoverage
    },
    //wa
    waFee: function () {
      
      //
      if (this.noFee(this.cifFee) ) {
        return ''
      }

      //18-55
      if (this.age < 18 || this.age > 55 ) {
        return '18-55'
      }

      //65
      if (this.age - 0 + this.year > 65) {
        return '65'
      }

      return waRates[`${this.sex}_year${this.year}_age${this.age}`] * 1 * (this.totalFee / 1000)
    },
    //wpa
    wpaFee: function () {
      
      //
      if (this.noFee(this.cifFee) ) {
        return ''
      }

      //
      if (this.relation !== 'parent' ) {
        return ''
      }

      //
      if (this.age > 18 ) {
        return '18'
      }

      return wpaRates[`age${this.applicantAge}_year${this.year}`] * 1 * (this.totalFee / 1000)
    },
    //wpb
    wpbFee: function () {
      
      //
      if (this.noFee(this.cifFee) ) {
        return ''
      }

      //
      if (this.relation === 'self' ) {
        return ''
      }

      //
      if (this.applicantAge > 55 ) {
        return '55'
      }

      return wpbRates[`${this.applicantSex}_year${this.year}_age${this.applicantAge}`] * 1 * (this.totalFee / 1000)
    },
    //wp
    wpFee: function () {
      
      //
      if (this.noFee(this.cifFee) ) {
        return ''
      }

      //wp
      if (this.relation !== 'self') {
        return ''
      }
      return wpRates[`age${this.applicantAge}_year${this.year}`] * 1 * (this.totalFee / 1000)
    },

    //HRC_HRDADDCAMRC
    totalFee: function() {
      let total = 0;
      if (!isNaN(this.cifFee) && this.cifFee > 0) {
        total += this.cifFee;
      }
      if (!isNaN(this.hrcFee) && this.hrcFee > 0 && !this.hasSocialSecurity) {
        total += this.hrcFee;
      }
      if (!isNaN(this.hrdFee) && this.hrdFee > 0 && this.hasSocialSecurity) {
        total += this.hrdFee;
      }
      if (!isNaN(this.addcFee) && this.addcFee > 0) {
        total += this.addcFee;
      }
      if (!isNaN(this.addcFee) && this.addcFee > 0 && !isNaN(this.amrcFee) && this.amrcFee > 0 ) {
        total += this.amrcFee;
      }
      return total;

    },
    //
    indus: function() {
      let industriesArr = [{
        value: industries[0].industory.split(' - ')[0], 
        label: industries[0].industory.split(' - ')[0], 
        children: [{
          value: industries[0].industory.split(' - ')[1],
          label: industries[0].industory.split(' - ')[1],
          children: [this.makeJob(industries[0])]
        }]
      }];
      const len = industries.length;
      for (let i = 1; i < len; i++) {
        let job = industries[i]
        let first = job.industory.split(' - ')[0];
        let second = job.industory.split(' - ')[1];

        if (first !== industries[i - 1].industory.split(' - ')[0]) {
          industriesArr.push({
            value: first, 
            label: first, 
            children: [{
              value: second,
              label: second, 
              children:[this.makeJob(job)]
            }]
          })
        } else if (second !== industries[i - 1].industory.split(' - ')[1]) {
          industriesArr[industriesArr.length - 1].children.push({
            label: second, 
            value: second,
            children: [this.makeJob(job)]
          })
        } else {
          let index = industriesArr[industriesArr.length - 1].children.length - 1;
          industriesArr[industriesArr.length - 1].children[index].children.push(this.makeJob(job))
        }
      }
      return industriesArr;
    },
    //
    ageGroup: function() {
      if (this.age <= 17) {
        return parseInt(this.age / 5);
      } else {
        return parseInt(this.age / 5) + 1;
      }
    },
    //
    jobs: function () {
      const that = this;
      return industries.filter(function(ele) {
        return ele.industory === that.indu;
      })
    },
    //
    planTop: function() {
      let max = this.ageGroup < 4 ? 4 : 7;
      let coverage = this.coverage;
      if (coverage < 2) {
        max = 1;
      } else if (coverage < 4) {
        max = 2;
      } else if (coverage < 10) {
        max = 3;
      } else if (coverage < 16) {
        max = 4;
      } else if (coverage < 20) {
        max = 5;
      }
      return max
    },
    //
    occupation: function() {
      return industries.find((ele) => {
          return ele.code === this.job[2];
      })
    }
  },
  methods: {
    getColorClass: function(val) {
      return val > 0 ? 'ok' : 'no'
    },
    noFee: function (fee) {
      return ! (fee > 0)
    },
    makeJob: function(obj) {
      return {
            value: obj.code,
            label: `${obj.work}(${obj.code},${obj.life},${obj.accident},${obj.hospital})`
          }
    },
    //
    transAge: function(str) {
      if (str.length === 6) {
        this.age = this.dateToAge(str)
      }
    },
    //
    transApplicantAge: function(str) {
      if (str.length === 6) {
        this.applicantAge = this.dateToAge(str)
      }
    },
    //
    dateToAge: function(str) {
      const today = new Date();
      const thisYear = today.getFullYear() % 2000;
      const year = str.substr(0, 2);
      const month = str.substr(2, 2) - 1;
      const day = str.substr(4, 2);
      const birthDate = new Date((year - 0 < thisYear ? '20' : '19') + year , month, day)
      const m = today.getMonth() - birthDate.getMonth();

      let age = today.getFullYear() - birthDate.getFullYear();
      if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
        age--;
      }
      return age;
    }

  }
}
</script>

<style lang="scss">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  width:1008px;
  color: #2c3e50;
  margin: 20px auto;
}
.logo {
  display: inline-block;
}
.theme {
  display: inline-block;
  margin-top: 0;
  margin-bottom: 30px;
  color: #d18f07;
}
.choice {
  margin-top: 8px;
}
.border_r {
  border-right: 1px solid #ccc;
}
.border_l {
  border-left: 1px solid #ccc;
}

.border_b {
  border-bottom: 1px solid #ccc;
}

.short {
  width: 60px;
}

.middle {
  width: 100px;
}
.el-cascader--small {
  width: 100%
}
.box {
  list-style-type: none;
  border: 1px solid #ccc;
  margin-bottom: 10px;
}
.title {
  width: 100%;
  height: 70px;
  background: #fff;
  color: orange;
  font-size: 24px;
  line-height: 70px;
  border-right: 1px solid #ccc;
}
.fee {
  color: sienna;
  background: #D3DCE6;
  padding: 8px 0;
  margin: 5px 8px;
  border-radius: 4px;

  .ok {
    color: green
  }

  .no {
    color: red;
  }
}
</style>



